div.home{
  height: 100%;
  background: url("../images/lion.jpg");
  background-position: left 30%;
  background-repeat: no-repeat;
  background-size: cover;
  filter: grayscale(30%);
  font-family:'Rock Salt', cursive;
}

.home span{
  position: absolute;
  bottom: 11%;
  left:5%;
  color: white;
  font-size: 28px;
  animation: text 1s linear ;
}

div.home h1{
  margin-top: 15px;
  font-size: 55px;
}

div.brush{
  position: absolute;
  bottom: 7%;
  left: 1.5%;
  height: 25%;
  width: 750px;
  background: url("../images/brush-stroke-banner.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position:center;
  animation: brush 2s linear ;
  transform: perspective(700px) rotateY(20deg);
}

@keyframes brush{

  0%{
    clip-path: inset(0px 750px 0px 0px);
    opacity:0;
  }

  25%{
    clip-path: inset(0px 750px 0px 0px);
    opacity:0;
  }

  50%{
    clip-path: inset(0px 750px 0px 0px);
    opacity:0;
  }

  75%{
    clip-path: inset(0px 0px 0px 0px);
    opacity: 1;
  }

  100%{
    clip-path: inset(0px 0px 0px 0px);
    opacity: 1;
  }
}

@keyframes text{

  0%{
    transform: scale(0);
  }

  100%{
    transform: scale(1);
  }
}


/*------------------------ Media Queries--------------------------- */


@media (max-width:1600px){

  .home span{
    font-size: 25px;
  }
  
  div.home h1{
    margin-top: 15px;
    font-size: 50px;
  }
  
  div.brush{
    bottom: 6%;
    left: 1%;
    width: 680px;
  }
}


