div.cover{
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  width: 220px;
  background: url(/Portfolio_website_developer_version/static/media/brush-stroke-banner.93993e10.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position:center;
  -webkit-clip-path: inset(0px 220px 0px 0px);
          clip-path: inset(0px 220px 0px 0px);
  padding: 40px 70px;
  transition: .5s;
}

a:hover + .cover {
  -webkit-clip-path:inset(0px 0px 0px 0px);
          clip-path:inset(0px 0px 0px 0px);
}


nav ul li a {
  position:absolute;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
