    font-size: 1.6vh;
    }
    
  div.icons{
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 48vh;
    width: 85vw;
    top:55%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    border-radius: 50%;
    box-shadow: 0px 0px 20px 2px black;
    overflow-y: scroll;
    scrollbar-width: none;
  }

  ::-webkit-scrollbar{
    display:none;
    }

  div.icons span:nth-child(1){
    margin-top:150px;
  }

  div.icons span{
    margin-bottom:150px;
    font-size: 5vh;
  }

  div.icons i {
    font-size: 10vh;

  }

  .scrollLabel i {
    position: absolute;
    display: block;
    top: 67%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-size: 40px;
    text-shadow: 0px 13px 4px rgba(0,0,0,0.3);
    -webkit-animation: scroll1 1.5s infinite linear ;
            animation: scroll1 1.5s infinite linear ;
  }

  .scrollEnd.scrollLabel i {
    top: 33%;
    -webkit-transform: translateX(-50%) rotate(180deg);
            transform: translateX(-50%) rotate(180deg);
    text-shadow: 0px -13px 4px rgba(0,0,0,0.3);
    -webkit-animation: scroll2 1.5s infinite linear ;
            animation: scroll2 1.5s infinite linear ;
  }

  @-webkit-keyframes scroll1{

    100%{
      top: 70%;
    }
  }

  @keyframes scroll1{

    100%{
      top: 70%;
    }
  }

  @-webkit-keyframes scroll2{

    100%{
      top: 30%;
    }
  }

  @keyframes scroll2{

