div.resume h1{
  padding: 1.5vh 0 ;
  text-align: center;
  letter-spacing: 5px;
}

div.resume h1 span {
  margin-left: 8px;
}

header.slider{
  width: 65vw;
  height: 70vh;
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0 0 2px 10px white, 0 0 45px black;
  background-color:#aaa;
  font-family: 'Special Elite', sans-serif;
  text-align: justify;
  text-justify: inter-character;
  word-wrap: break-all;
  word-spacing: 3px;
  overflow: hidden;
}

header.slider .backImg{
  height: 100%;
  width:100%;
  -webkit-filter: blur(2px);
          filter: blur(2px);
  opacity: 0.5;
  background-position: center;
  background-size: cover;
}

header.slider h2{
  position: absolute;
  top:7%;
  left:50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 2vw;
  letter-spacing: 5px;
}

header.slider p {
  position: absolute;
  top:17%;
  left:50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 1.5vw;
  line-height:1.7;
  width:80%;
}

header.slider button{
  position: absolute;
  top:50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 10px 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

header.slider button i {
  color: white;
  font-size: 35px;
  transition: .2s linear;
}
  
header.slider button i:hover{
  color: red;
  font-size: 38px;
}

header.slider button:nth-of-type(1){
  left:1%;
} 

header.slider button:nth-of-type(2){
  right:1%;
} 

header.slider .dots {
  position: absolute;
  bottom:5%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

header.slider .dots span{
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 10px;
  background-color: black;
  border-radius: 50%;
  box-shadow: 0 0 3px 5px white;
}

header.slider .dots span.activeDot{
  box-shadow: 0 0 3px 5px white, 0 0 4px 8px red;
}


